.searchBoxBackground {
  position: absolute;
  top: 5vh;
  right: 10vw;
  width: 20vw;
  height: 5vh;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  .searchBox {
    position: relative;
    width: 100%;
    height: 5vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    left: 80%;
    background-color: #000;
    z-index: 11;
    .searchIcon {
      width: 2vw;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      background-color: transparent;
      border: none;
      cursor: pointer;
      margin: 0 2%;
      button {
        color: white;
        background-color: transparent;
        border: none;
        &:hover {
          color: #c7c7c7;
          transform: scale(1.2);
          transition: 0.1s ease-in;
        }
      }
    }
    .closeIcon {
      color: white;
      background-color: transparent;
      border: none;
      cursor: pointer;
      &:hover {
        color: #c7c7c7;
        transform: scale(1.2);
        transition: 0.1s ease-in;
      }
    }
    .searchField {
      width: 50%;
      height: 2vh;
      font-size: 0.5em;
      letter-spacing: 0.1em;
      margin: 0 5%;
      padding-left: 1vw;
      color: #272727;
      border: none;

      -webkit-appearance: none;
      border-radius: 0;
    }
  }
}

/**************************************** MEDIA QUERIES ********************************************/
@media all and (max-width: 1024px) {
  .searchBoxBackground {
    .searchBox {
      left: 85%;
      .searchField {
        width: 68%;
      }
    }
  }
}
@media all and (max-width: 768px) {
  .searchBoxBackground {
    width: 37%;
    height: 15vh;
    top: 0;
    .searchBox {
      position: relative;
      width: 100%;
      height: 10vh;
      left: 93%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      background-color: #000;

      .searchIcon {
        font-size: 0.8em;
        margin: 0;
      }
      .searchField {
        height: 3vh;
        font-size: 0.8em;
        padding-left: 2vw;
        -webkit-appearance: none;
        border-radius: 0;
        &[type="text"] {
          font-size: 0.8em;
        }
      }
    }
  }
}
@media all and (max-width: 414px) {
  .searchBoxBackground {
    width: 70%;
    height: 15vh;
    .searchBox {
      position: relative;
      width: 100%;
      height: 15vh;
      left: 88%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      background-color: #000;
      .searchIcon {
        font-size: 0.8em;
        margin: 0 6%;
      }
      .searchField {
        width: 60%;
        height: 4vh;
        margin: 0 2vw;
        padding-left: 5vw;
        -webkit-appearance: none;
        border-radius: 0;
      }
    }
  }
}
@media all and (max-width: 823px) and (orientation: landscape) {
  .searchBoxBackground {
    width: 35%;
    height: 15vh;
    .searchBox {
      position: relative;
      width: 100%;
      height: 10vh;
      left: 92%;
      .searchField {
        width: 60%;
        height: 4vh;
        margin: 0 1vw;
        font-size: 0.8em;
        padding-left: 1vw;

        &::placeholder {
          padding-left: 0vw;
          font-size: 0.8em;
        }
      }
    }
  }
}
